<template>
  <div
    class="vu-time-picker form-group"
    :class="classes"
  >
    <label
      class="control-label"
      v-if="label.length"
    >
      {{ label }}
      <span
        v-if="required"
        class="label-field-required"
      > *</span>
    </label>
    <template v-if="useNativeInput || isMobileOrTablet">
      <div>
        <input
          v-bind="$attrs"
          :value="value"
          :placeholder="placeholder"
          :disabled="disabled"
          type="time"
          class="vu-time-picker__display-native form-control"
          style="width: fit-content;"
          @input="({target}) => { if(!vuInputComposition) { target.composing = false } $emit('update:modelValue', target.value); }"
        >
      </div>
    </template>
    <VuPopover
      v-else-if="!disabled "
      class="vu-time-picker__popover"
      style="width: fit-content;"
      :show="isPopoverOpen"
    >
      <div class="vu-time-picker__display form-control">
        <span>{{ hours }}</span>:<span>{{ minutes }}</span>
      </div>
      <template #body>
        <div class="vu-time-picker__body">
          <div class="vu-time-picker__hours">
            <label
              v-for="hour in [...Array(24).keys()]"
              :key="hour"
              :class="{'vu-time-picker__hours--selected' : hours === formatNumberForTime(hour) }"
            >
              <span>{{ formatNumberForTime(hour) }}</span>
              <input
                type="radio"
                name="hours"
                :value="formatNumberForTime(hour)"
                v-model="hours"
              >
            </label>
          </div>
          <div class="vu-time-picker__minutes">
            <label
              v-for="minute in [...Array(60).keys()]"
              :key="minute"
              :class="{'vu-time-picker__minutes--selected' : minutes === formatNumberForTime(minute) }"
            >
              <span>{{ formatNumberForTime(minute) }}</span>
              <input
                type="radio"
                name="minutes"
                :value="formatNumberForTime(minute)"
                v-model="minutes"
              >
            </label>
          </div>
        </div>
      </template>
    </VuPopover>
    <div
      v-else
      class="form-control vu-time-picker__display"
      disabled
    >
      <span>{{ hours }}</span>:<span>{{ minutes }}</span>
    </div>
    <span
      v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text"
    >
      {{ error }}
    </span>
    <span
      v-if="helper.length"
      class="form-control-helper-text"
    >
      {{ helper }}
    </span>
  </div>
</template>

<script>
import inputable from '../mixins/inputable';
import validatable from '../mixins/validatable';
import disablable from '../mixins/disablable';
import { RegistrableInput } from '../mixins/registrable';
import VuPopover from './vu-popover.vue';
  
export default {
  name: 'vu-time-picker',
  inheritAttrs: false,
  inject: {
    vuInputComposition: {
      default: false,
    },
  },
  mixins: [inputable, validatable, disablable, RegistrableInput],
  props: {
    useNativeInput: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  emits: ['update:modelValue'],
  data() {
    return {
      minutes: '00',
      hours: '00',
      isPopoverOpen: false,
    };
  },
  computed: {
    isMobileOrTablet() {
      if (!window)
        return false;
      if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        return true;
      }
      if (navigator.platform === 'MacIntel' && navigator.maxTouchPoints > 1) {
        return true;
      }
      return false;
    },
  },
  watch: {
    modelValue(newVal) {
      const [hours, minutes] = this.splitTime(newVal);
      this.hours = hours;
      this.minutes = minutes;
    },
    minutes(newVal) {
      this.$emit('update:modelValue', `${this.hours}:${newVal}`);
    },
    hours(newVal) {
      this.$emit('update:modelValue', `${newVal}:${this.minutes}`);
    },
  },
  beforeMount() {
    const [hours, minutes] = this.splitTime(this.modelValue);
    this.hours = hours;
    this.minutes = minutes;
  },
  methods: {
    splitTime(str) {
      return str.split(':');
    },
    formatNumberForTime(int) {
      return int < 10 ? `0${int}` : `${int}`;
    },
  },
  components: { VuPopover, VuPopover }
};
</script>
  
<style lang="scss" scoped>
.vu-time-picker{
  display: flex;
  flex-direction: column;
}
.vu-time-picker__display{
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
  cursor: pointer;
  &[disabled]{
    user-select: auto; // when the input is disable we need to be able to select the text
    cursor: default;
  }
}
.vu-time-picker__body{
  --selected-bgc:  #2E76FF;
  --selected-color:  #FFF;
  --selected-border-color:  #101010;
  --hover-bgc:  #C0D6FF;

    input[type=time]{
      visibility: hidden;
    }
    max-height: 200px;
    overflow: hidden;
    display: flex;
    gap:2px;
    .vu-time-picker__hours,.vu-time-picker__minutes{
      user-select: none;
      max-height: 100%;
      overflow-y: scroll;
      display: flex;
      flex-direction: column;
      -ms-overflow-style: none;  /* Internet Explorer 10+ */
      scrollbar-width: none;

      &::-webkit-scrollbar { 
        display: none;  /* Safari and Chrome */
      }

      label{
        padding: 4px 8px;
        cursor: pointer;
        input[type=radio]{
          display: none;
        }
        font-weight: normal;

        &.vu-time-picker__hours--selected, &.vu-time-picker__minutes--selected{
          background-color: var(--selected-bgc);
          color: var(--selected-color);
          font-weight: bold;
          box-shadow: inset 0px 0px 0px 2px var(--selected-border-color);

          &:hover{
            background-color: var(--selected-bgc);
          }
        }
        &:hover{
          background-color: var(--hover-bgc);
        }   
      }
    }
  }
  //native input 
.vu-time-picker__display-native {
  padding-left: 10px;
  padding-right: 10px;
  appearance: none ;
  text-align: center;
  vertical-align:middle;
} 
  </style>
