<template>
  <div
    class="form-group"
    :class="classes"
  >
    <label
      class="control-label"
      v-if="label.length"
    >{{ label }}<span
      v-if="required"
      class="label-field-required"
    > *</span></label>
    <div
      v-click-outside="function () { open = false; search = (value && selected.label) || value}"
      @click="open = !open && !disabled; search = (value && selected.label) || value"
      :class="[
        'vu-select',
        'select', {
          'select-placeholder': !autocomplete,
          'select-no-placeholder-option': hidePlaceholderOption,
          'select-not-chosen': !autocomplete && !value,
          'dropdown-visible': open,
          'select-disabled': disabled,
          'select-autocomplete': autocomplete,
          'select-clearable': clearable,
          'select-focus': focused && !disabled
        }]"
    >
      <input
        v-if="autocomplete"
        :disabled="disabled"
        :placeholder="selected.label"
        class="form-control"
        v-model="search"
      >
      <VuIconBtn
        v-if="value && (autocomplete || clearable)"
        icon="clear"
        class="select__clear-icon"
        :class="{'select--has-handle': autocomplete }"
        @click="$emit('update:modelValue', ''); search = ''"
      />
      <div
        v-if="!autocomplete"
        class="select-handle"
      />
      <select
        v-if="!autocomplete"
        class="form-control select-hidden"
        :disabled="disabled"
        ref="nativeSelect"
        @focus="focused = true"
        @blur="blur()"
        @keydown="(event) => innerSelectKeydown(event)" 
      />
      <div
        v-if="!autocomplete"
        class="select-handle"
      />
      <ul
        v-if="!autocomplete"
        class="select-choices form-control"
      >
        <li class="select-choice">
          {{ selected.label }}
        </li>
      </ul>
      <div
        v-if="attach && open"
        class="select-dropdown"
        ref="dropdown"
        :style="`height: ${38 * (innerOptions.length + (!autocomplete && !hidePlaceholderOption ? 1 : 0))}px; max-height: ${38 * (internMaxVisible + 1)}px;`"
      >
        <ul class="select-results">
          <li
            v-if="!autocomplete && !hidePlaceholderOption"
            class="result-option result-option-placeholder"
            :class="{'result-option-selected': !modelValue }"
            @click="$emit('update:modelValue', ''); search = ''"
          >
            {{ placeholder }}
          </li>
          <template v-if="!grouped">
            <li
              v-for="option in innerOptions"
              :key="`${uid}-${ option.value || option.label }`"
              :class="{
                'result-option-disabled': option.disabled,
                'result-option-selected': (option.value === value)
              }"
              class="result-option"
              @click="option.disabled ? null : $emit('update:modelValue', option.value); search = option.label"
            >
              {{ option.label }}
            </li>
          </template>
          <template v-else>
            <li
              v-for="(options, groupName) in groupedOptions"
              :key="`${uid}-${options.group}`"
              class="result-group"
            >
              <span class="result-group-label">{{ groupName }}</span>
              <ul class="result-group-sub">
                <li
                  v-for="option in options"
                  :key="`${uid}-${option.value}`"
                  :class="{
                    'result-option-disabled': option.disabled,
                    'result-option-selected': (option.value === value)
                  }"
                  class="result-option"
                  @click="option.disabled ? null : $emit('update:modelValue', option.value)"
                >
                  {{ option.label }}
                </li>
              </ul>
            </li>
          </template>
        </ul>
      </div>
      <VuPopover
        v-else-if="(willDetach) && open"
        :attach="attach"
        type="vu-select-dropdown"
        :show="open"
        :positions="['bottom-left', 'top-left']"
        :side="'bottom-left'"
        :sync-width="true"
        :animated="false"
        :content-class="contentClass"
        :offsets="{ 'bottom-left' : { y: 3 }, 'top-left' : { y: -43 }}"
        :content-style="[{ zIndex: dropdownZIndex }, 'position: absolute;', contentStyle]"
      >
        <template #body>
          <VuScroller
            always-show
          >
            <VuSelectOptions
              v-bind="{ options: innerOptions, selected: [selected], placeholder }" 
              @click-item="(e) => $emit('update:modelValue', e.value)"
            />
          </VuScroller>
        </template>
      </VuPopover>
    </div>
    <span
      v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text"
    >
      {{ error }}
    </span>
    <span
      v-if="helper.length"
      class="form-control-helper-text"
    >{{ helper }}</span>
  </div>
</template>
<script>
import inputable from '../mixins/inputable';
import clearable from '../mixins/clearable';
import validatable from '../mixins/validatable';
import disablable from '../mixins/disablable';
import { RegistrableInput } from '../mixins/registrable';
import validateAttachTarget from '../utils/validateAttachTarget';
import { uuidv4 } from '../utils/guid';
import VuIconBtn from './vu-icon-btn.vue';
import VuPopover from './vu-popover.vue';
import VuSelectOptions from './vu-select-options.vue';
import VuScroller from './vu-scroller.vue';

export default {
  name: 'vu-select',
  inheritAttrs: false,
  mixins: [inputable, clearable, disablable, validatable, RegistrableInput],
  props: {
    autocomplete: {
      type: Boolean,
      default: () => false,
    },
    hidePlaceholderOption: {
      type: Boolean,
      default: () => false,
    },
    grouped: {
      type: Boolean,
      default: () => false,
    },
    maxVisible: {
      type: Number,
      default: () => 5,
    },
    dropdownZIndex: {
      type: Number,
      default: 1020,
    },
    // detachable props
    attach: {
      default: () => true,
      validator: validateAttachTarget,
    },
    contentClass: {
      type: [String, Object],
      default: '',
    },
    contentStyle: {
      type: [String, Object],
      default: () => '',
    },
    // end detachable
  },
  emits: ['update:modelValue'],
  data: () => ({
    open: false,
    focused: false,
    search: '',
    uid: uuidv4(),
  }),
  watch: {
    value() {
      this.search = this.selected.label;
    },
    open(value) {
      if (value) {
        this.focus();
      }
    },
  },
  created() { this.search = (this.value && this.selected.label) || this.value; },
  computed: {
    isIos() {
      if (!window) {
        return false;
      }
      if (/Android|webOS|iPhone|iPad|iPod/i.test(navigator.userAgent)) {
        return true;
      }
      if (navigator.platform === 'MacIntel' && navigator.maxTouchPoints > 1) {
        return true;
      }
      return false;
    },
    hasSomeEnabledOptions() {
      return this.enabledOptions.length > 0;
    },
    firstEnabledOption() {
      return this.enabledOptions.slice(0)[0];
    },
    lastEnabledOption() {
      return this.enabledOptions.slice(-1)[0];
    },
    enabledOptions() {
      const options = (this.autocomplete && this.search) ? this.options : this.innerOptions;
      return options.filter((el) => !el.disabled);
    },
    innerOptions() {
      return this.autocomplete
        ? this.options
          .filter((el) => (el.label.toLowerCase().includes(this.search.toLowerCase()) || el.value.toLowerCase().includes(this.search.toLowerCase())))
        : this.options;
    },
    selected() {
      return this.options.find((el) => el.value === this.value) || {
        label: this.placeholder,
      };
    },
    willDetach() {
      return this.attach === false || this.attach !== '' && typeof this.attach === String;
    },
    groupedOptions() {
      return this.grouped ? this.options.reduce((acc, el) => {
        if (!acc[el.group])
          acc[el.group] = [];
        acc[el.group].push(el);
        return acc;
      }, {}) : null;
    },
    internMaxVisible() {
      return this.maxVisible > this.options.length ? this.options.length : this.maxVisible;
    },
  },
  methods: {
    innerSelectKeydown(e) {
      switch (e.code) {
        case 'Space':
        case 'Enter':
        case 'NumpadEnter':
          this.open = !this.open;
          e.preventDefault();
          e.stopPropagation();
          break;
        case 'Escape':
          this.open = false;
          e.preventDefault();
          e.stopPropagation();
          break;
        case 'ArrowUp':
          this.browse();
          e.stopPropagation();
          break;
        case 'ArrowDown':
          if (!this.open) {
            this.open = true;
          }
          else {
            this.browse(true);
          }
          e.stopPropagation();
          break;
        default:
          break;
      }
    },
    focus() {
      this.focused = true;
      const innerSelect = this.$refs && this.$refs.nativeSelect;
      if (innerSelect && !this.isIos) {
        innerSelect.focus();
      }
    },
    blur() {
      this.focused = false;
    },
    async browse(next) {
      if (!this.grouped) {
        if (!this.hasSomeEnabledOptions) {
          return;
        }
        if (this.firstEnabledOption === this.lastEnabledOption) {
          this.value = !this.modelValue ? this.firstEnabledOption.value : undefined;
        }
        else if (!next && this.selected === this.firstEnabledOption) {
          this.value = (this.hidePlaceholderOption) ? this.lastEnabledOption.value : undefined;
        }
        else if (next && this.selected === this.lastEnabledOption) {
          this.value = (this.hidePlaceholderOption) ? this.firstEnabledOption.value : undefined;
        }
        else if (!this.modelValue) {
          this.value = next ? this.firstEnabledOption.value : this.lastEnabledOption.value;
        }
        else {
          let index = this.enabledOptions.indexOf(this.selected);
          index += (next ? 1 : -1);
          const newIndex = this.innerOptions.indexOf(this.enabledOptions[index]);
          this.value = this.innerOptions.slice(newIndex, newIndex + 1)[0].value;
        }
        this.$nextTick(() => {
          const dropdown = this.$refs && this.$refs.dropdown;
          let domOption;
          if (dropdown) {
            domOption = this.$refs?.dropdown.querySelector('ul li.result-option-selected');
          }
          if (domOption) {
            const optBottom = domOption.offsetTop + domOption.clientHeight;
            if (optBottom > dropdown.scrollTop + dropdown.clientHeight
                            || optBottom < dropdown.scrollTop) {
              this.$refs.dropdown.scrollTo({ top: domOption.offsetTop });
            }
          }
        });
      }
    },
  },
  components: { VuIconBtn, VuPopover, VuSelectOptions, VuScroller }
};
</script>
<style scoped lang="scss">
  @import '../styles/_variables.scss';

  .form-group .form-control {
    margin-bottom: 0px;
  }

  .select-dropdown {
    overflow-y: auto;
    overscroll-behavior: contain;
  }

  .select.select-autocomplete:before {
    display: none;
  }

  .select.select-autocomplete:after {
    display: none;
  }

  .select__clear-icon {
    position: absolute;
    color: #77797C;
    font-size: 18px;
    bottom: 0;
    right: 0;

     &:not(.select--has-handle) {
      right: 38px;
    }
  }

  .select-placeholder.select-no-placeholder-option .select-results > .result-option:first-child {
    background-color: inherit;
  }

  .select-placeholder .select-results > .result-option:first-child:hover {
    background-color: $blue-3;
    color: white;
    }

  .vu-select ::v-deep .vu-popover__activator {
    height: 0;
    width: 100%;
    position: relative;
    display: block;
  }
    
  .vu-select-dropdown ul {
    margin-bottom: 0;
  }

  .select-disabled ul {
    // [disabled]
    &, &.form-control:hover {
      border-color: $disabled-light;
    }

    cursor: default;
    user-select: none;
  }

</style>
<style lang="scss">
// Put this in Popover ?
.vu-select-dropdown {
  .vu-select-dropdown-body {
    box-shadow: 0 1px 2px #B4B6Ba;
    background-clip: padding-box;
  }
}
</style>
