<template>
  <div
    v-mask="loading"
    class="accordion-container"
  >
    <div
      :class="['accordion accordion-root', {
        filled: filled,
        'filled-separate': separated,
        divided: divided,
        styled: outlined,
        animated: animated,
      }]"
    >
      <div
        v-for="item in items"
        :key="`${ guid }-accordion-${item}`"
        :class="['accordion-item', { active: value.includes(item) }]"
      >
        <div
          @click="toggle(item)"
          class="accordion-title "
        >
          <i class="caret-left" />
          <slot :name="'title-' + item" />
        </div>
        <div
          v-if="keepRendered || value.includes(item)"
          v-show="value.includes(item)"
          class="content-wrapper"
        >
          <div :class="['content', { 'accordion-animated-content' : animated}]">
            <slot :name="'item-' + item" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import guid from '../utils/uuid';
import loadable from '../mixins/loadable';

export default {
  name: 'vu-accordion',
  mixins: [loadable],
  props: {
    modelValue: {
      type: Array,
      default: () => [],
    },
    items: {
      type: Number,
      default: () => 0,
    },
    open: {
      type: Boolean,
      default: () => false,
    },
    filled: {
      type: Boolean,
      default: () => false,
    },
    divided: {
      type: Boolean,
      default: () => false,
    },
    outlined: {
      type: Boolean,
      default: () => false,
    },
    separated: {
      type: Boolean,
      default: () => false,
    },
    animated: {
      type: Boolean,
      default: () => false,
    },
    exclusive: {
      type: Boolean,
      default: () => false,
    },
    keepRendered: {
      type: Boolean,
      default: () => false,
    },
  },
  emits: ['update:modelValue'],
  data: () => ({
    guid,
  }),
  created() {
    if (this.open && !this.exclusive) {
      let i = this.items;
      const values = [];
      while (i) values.push(i--);
      this.$emit('update:modelValue', values);
    }
  },
  computed: {
    value() {
      return this.modelValue;
    },
  },
  methods: {
    toggle(item) {
      if (this.value.includes(item)) {
        const values = this.value.slice();
        values.splice(values.indexOf(item), 1);
        this.$emit('update:modelValue', values);
      } else if (this.exclusive) {
        this.$emit('update:modelValue', [item]);
      } else {
        this.$emit('update:modelValue', [item].concat(this.value || []));
      }
    },
  },
};
</script>
