<template>
  <div
    class="form-group"
    :class="classes"
  >
    <label
      class="control-label"
      v-if="label.length"
    >{{ label }}<span
      v-if="required"
      class="label-field-required"
    > *</span></label>
    <textarea
      :value="value"
      :placeholder="placeholder"
      :disabled="disabled"
      :rows="rows"
      class="form-control"
      @input="(e) => $emit('update:modelValue', e.target.value)"
    />
    <p
      v-for="(error, pos) in errorBucket"
      :key="`${pos}-error-${error}`"
      style="display: block"
      class="form-control-error-text"
    >
      {{ error }}
    </p>
    <span
      v-if="helper.length"
      class="form-control-helper-text"
    >{{ helper }}</span>
  </div>
</template>

<script>
import inputable from '../mixins/inputable';
import validatable from '../mixins/validatable';
import disablable from '../mixins/disablable';

import { RegistrableInput } from '../mixins/registrable';

export default {
  name: 'vu-textarea',
  mixins: [inputable, disablable, validatable, RegistrableInput],
  props: {
    rows: {
      type: [Number, String],
      default: () => 2,
    },
  },
  emits: ['update:modelValue'],
};
</script>

<style scoped>
  textarea {
    resize: none;
  }
</style>
